import React, {useEffect} from 'react';
import {observer} from "mobx-react";
import {Col, Divider, Form, Input, Modal, Row, Select} from "antd";
import Command from "../Logic/Command";
import M12M14 from "./Select/M12M14";
import M5M6 from "./Select/M5M6";
import M11 from "./Select/M11";
import M13 from "./Select/M13";
import M1M15 from "./Select/M1M15";
import M2M3M4 from "./Select/M2M3M4";
import M7M8M9M10 from "./Select/M7M8M9M10";

const RowSettings = observer(({
                                  rowSettingsVisible,
                                  setRowSettingsVisible,
                                  command,
                                  curRow,
                                  Rom,
                                  EInvMaskM2,
                                  ECondM3,
                                  EJumpM4,
                                  EShiftControlM7,
                                  ECarryM8,
                                  EOperandsM9,
                                  EFuncM10,
                                  EResultM11,
                                  EInputM12,
                                  EPswM13,
                                  EOutputM14
                              }) => {

    const [form] = Form.useForm();

    useEffect(() => {
            console.log(EJumpM4);
            if (command[curRow] === undefined){}
            else{
            form.setFieldsValue({
                m1: command[curRow].m1,
                m2: command[curRow].m2,
                m3: command[curRow].m3,
                m4: command[curRow].m4,
                m5: command[curRow].m5,
                m6: command[curRow].m6,
                m7: command[curRow].m7,
                m8: command[curRow].m8,
                m9: command[curRow].m9,
                m10: command[curRow].m10,
                m11: command[curRow].m11,
                m12: command[curRow].m12,
                m13: command[curRow].m13,
                m14: command[curRow].m14,
                m15: command[curRow].m15
            });}

    }, []);


    const onCancel = (() => {
        console.log(command[curRow]);
        setRowSettingsVisible(false);
    });

    const saveCommand = (data, curRow) => {
        command[curRow] = data;
        console.log(command[curRow]);
    };

    const onOk = (() => {
        let data = form.getFieldsValue();
        let saveData;
        // data.reg = parseInt(data.reg, 10);
        data.m1 = parseInt(data.m1, 10);
        data.m5 = parseInt(data.m5, 2);
        data.m6 = parseInt(data.m6, 2);
        data.m15 = parseInt(data.m15, 10);

        Rom[curRow].SetFields(data);

        saveData = data;
        saveData.key = curRow;

        saveCommand(saveData, curRow);
        // form.resetFields();
        // console.log(Rom);
        // console.log(Rom[0].Parse(curRow));
        setRowSettingsVisible(false);
    });


    return <>
        <Modal
            visible={rowSettingsVisible}
            title="Create a new command"
            okText="Create"
            cancelText="Cancel"
            width={1800}
            onCancel={onCancel}
            onOk={onOk}
            form={form}
        >
            <Form
                form={form}
                layout="vertical"
                name="form"
                wrapperCol={{
                    span: 50,
                }}
            >

                <Row justify="center">
                    <Col>
                        Входная и выходные шины
                        <M12M14 EInputM12={EInputM12} EOutputM14={EOutputM14}/>
                    </Col>
                    <Divider type="vertical" style={{height: "200px", backgroundColor: "#000"}}/>
                    <Col>
                        Мультиплексоры адреса
                        <M5M6/>
                    </Col>
                    <Divider type="vertical" style={{height: "200px", backgroundColor: "#000"}}/>
                    <Col>
                        Приёмник результата {/*Исправить*/}
                        <M11 EResultM11={EResultM11}/>
                    </Col>
                    <Divider type="vertical" style={{height: "200px", backgroundColor: "#000"}}/>
                    <Col>
                        Формирование PSW
                        <M13 EPswM13={EPswM13}/>
                    </Col>
                    <Divider type="vertical" style={{height: "200px", backgroundColor: "#000"}}/>
                    <Col>
                        Поля констант
                        <M1M15/>
                    </Col>
                </Row>
                <Divider type="horizontal" className="divider-horizontal"
                         style={{width: "100vw", backgroundColor: "#000", margin: "0px"}}/>
                <Row justify="center">
                    <Col>
                        Переходы и условия переходов
                        <M2M3M4 EInvMaskM2={EInvMaskM2} ECondM3={ECondM3} EJumpM4={EJumpM4} form={form}/>
                    </Col>
                    <Divider type="vertical" style={{height: "400px", backgroundColor: "#000"}}/>
                    <Col>
                        Операции над данными
                        <M7M8M9M10 EShiftControlM7={EShiftControlM7} ECarryM8={ECarryM8} EOperandsM9={EOperandsM9}
                                   EFuncM10={EFuncM10}/>
                    </Col>
                </Row>
            </Form>
        </Modal>
    </>
});
export default RowSettings;